/* Estilos generales para la página */
#fuel-calculator {
    max-width: 100%; /* Ajusta el ancho a 100% */
    margin: 0 auto;
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    font-family: 'Arial', sans-serif;
}

/* Título principal */
#fuel-calculator h2 {
    text-align: center;
    font-size: 24px;
    color: #4A90E2;
    margin-bottom: 20px;
}

/* Estilo para los formularios */
form {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

/* Estilo para los campos de entrada y selección */
form div {
    margin-bottom: 0px; /* Reducir el espacio entre las filas */
}

label {
    font-size: 1rem;
    color: #555;
    margin-bottom: 5px;
    display: inline-block;
}

/* Aseguramos que cada campo ocupe el mismo espacio */
input, select, label {
    margin-bottom: 5px; /* Reducir el margen inferior de cada campo */
    padding: 6px; /* Reducir padding para ahorrar espacio */
    font-size: 0.9rem; /* Ajustar tamaño de la fuente */
}

input:focus, select:focus {
    border-color: #4A90E2;
    box-shadow: 0 0 5px rgba(74, 144, 226, 0.3);
    outline: none;
}

/* Alineación de campos de km y precio por galón en la misma línea */
.unit-section {
    display: flex;
    gap: 15px; /* Reducido para menos espacio */
}

.unit-section div {
    flex: 1;
}

/* Alineación de No. unidad, placa y tipo de unidad en la misma línea */
.inline-fields {
    display: flex;
    gap: 15px; /* Reducido para menos espacio */
}

.inline-fields div {
    flex: 1;
}

/* Alineación de ruta y empresa en la misma línea */
.route-company-container {
    display: flex;
    gap: 15px; /* Reducido para menos espacio */
}

.route-company-container div {
    flex: 1;
}

/* Estilos para los resultados redondeados */
#fuel-calculator .rounded-result {
    font-size: 1.2rem;
    color: #333;
    text-align: center;
    margin-top: 20px;
}

/* Estilos para la sección de precio por galón */
input[name="pricePerGallon"] {
    padding-left: 35px;
    background-image: url('https://upload.wikimedia.org/wikipedia/commons/5/5f/Guatemala_symbol.png');
    background-repeat: no-repeat;
    background-position: 10px center;
    background-size: 20px 20px;
}

/* Sección de fecha y hora */
.date-time {
    position: fixed;
    bottom: 10px;
    right: 10px;
    font-size: 14px;
    color: #333;
}

.date-time-container {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.date-time-container input {
    width: 100%;
    padding: 8px;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 5px;
}

/* Ajuste para el campo de fecha congelada debajo de piloto */
.pilot-date-time {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
}

.pilot-date-time button {
    margin-top: 10px;
}

/* Responsividad para dispositivos pequeños */
@media (min-width: 768px) {
    #fuel-calculator .form-row {
        flex-direction: row; /* Volver a columna horizontal en pantallas grandes */
    }


    /* Estilo para las secciones de formularios cuando se agrupan en columna */
    .unit-section div, .inline-fields div, .route-company-container div {
        width: 100%; /* Utilizar el 100% del ancho */
    
}

    /* Ajustamos los campos dentro de estas secciones para tener un ancho de 40px en pantallas pequeñas */
    .unit-section div, .inline-fields div, .price-gallons-container div, .route-company-container div {
        width: 40px; /* Establecemos un ancho de 40px */
        flex: 0 0 40px; /* Evitamos que los elementos se expandan */
    }

    /* Ajustar los inputs, selects y etiquetas para reducir el espacio */
 input, select, label {
    font-size: 0.9rem;
    padding: 6px;
    margin: 0;
}

    /* Asegurarse de que el diseño se vea bien en pantallas pequeñas */
    button {
        padding: 8px 14px;
        font-size: 1rem;
    }

    /* Reducción de margen entre botones */
    button + button {
        margin-top: 8px; /* Reducir el margen entre botones */
    }
}

@media (max-width: 480px) {
    input, select {
        padding: 10px;
        font-size: 0.9rem;
    }

    button {
        padding: 10px;
        font-size: 0.9rem;
    }

    label {
        font-size: 0.9rem;
    }
}


#fuel-calculator .form-row {
    display: flex;
    flex-direction: column; /* Cambiar a columna para pantallas pequeñas */
    gap: 8px; /* Reducir el espacio entre los elementos */
    margin-bottom: 0;
}
  
  #fuel-calculator input, #fuel-calculator select, #fuel-calculator label {
    margin-bottom: 2px !important; /* Reducir márgenes entre campos */
    padding: 4px !important; /* Reducir padding de los campos */
  }